
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap');

*{
  margin:0;
  padding:0;
  border:0;
  outline:0;
  box-sizing: border-box;
  list-style-type: none;
  text-decoration:none;

}
:root{
  --color-bg:#030303;
  --color-bg-variant:#dae721;
  --color-primary:#4db5ff;
  --color-primary-variant:rgb(215, 240, 20);
  --color-white:#fff;
  --color-light:rgba(255, 255, 255, 0.589);

  --transition :all 400ms ease;

  --cointainer-width-lg:75%;
  --container-width-md:86%;
  --cointainer-width-ms:90%;

}
html{
  scroll-behavior: smooth;
}
::webkit-scrolbar{
  display: none;


}
body{
  font-family: 'Poppins', sans-serif;
  background:var(--color-bg);
  color:var(--color-white);
  line-height:1.7;
  /* background-image: url(../src/assests/1.JPG); */

}
/*============general styles===========*/
.cointainer{
  width:var(--cointainer-width-lg);
  margin:0 auto;

}
h1,
h2,
h3,
h4,
h5{
  font-weight:5000;
}
h1{
  font-size: 2.5rem;
}
section{
  margin-top: 8rem;
  
} 
section >h2, section>h5{
  text-align: center;
  color:var(--color-primary-variant);
 

}
section > h2 {
  color:var(--color-primary);
  margin-bottom :3rem;

} 
.text-light{
  color:var(--color-primary);
  transition:var(--transition) ;
}
a{
  color:var(--color-primary);
  transition:all 400ms ease;
}
a:hover{
  color:var(--color-white);

}
.btn{
  width:max-content;
  display: inline-block;
  color: var(--color-primary);
  padding:0.75rem 1.2rem ;
  border-radius:0.4rem;
  cursor: pointer;
  border: 1px solid var(--color-primary);
  transition: var(--transition);
}
.btn:hover{
  background: var(--color-white);
  color:var(--color-bg);
  border-color: transparent;
}
.btn-primary{
  background:var(--color-primary);
  color:var(--color-bg)
}
.img{
  display: block;
  width: 100%;
  object-fit: cover;

}
/*====media quies(medium devics)============*/
@media screen and (max-width:1024px){
  .cointainer{
    width:var(--cointainer-width-md);
  }
  section {
    margin-top: 8rem;
  
  }
}    

/*====media quies(small devics)============*/
@media screen and (max-width:600px){
  .cointainer{
    width:var(--cointainer-width-sm);
  }
  section > h2{
    margin-bottom: 3rem;
  }
} 